:root
{
  --trans: 1s ease;
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

body.dark-mode{
  background-color: black;
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    font-weight: bold;
    color: var(--black);  
}


  

.button{
  color: white;
  font-size: 16px;
  border: 1px solid var(--orange);
  border-radius: 34px;
  padding: 11px 26px;
  cursor: pointer;
  background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
  box-shadow: 0px 4px 24px 3px rgba(251, 161, 40, 0.42);
}

.button:hover{
  color: var(--orange);
  background: white;
  border: 1px solid var(--orange);
}

@media screen and (max-width:480px) {
  .App{
    padding: 0.4rem 1rem;
  }
}